import React from "react";
import Button from "./Button";

function Card(props) {
    const products = props.newDatabase;
    const setFunc = props.setF;


    return (
        <div className="card">
            <div className="card-image">
                <img src={products.image} alt="Products"/>
            </div>
            <div className="card-info">
                <div className="card-name">{products.name}</div>
                <div className="card-price">{products.price}</div>
                <div className="card-button">
                    <Button newProduct={products} setterFunction={setFunc}/>
                </div>
            </div>
         </div>

    );
}
export default Card;