import React, {useState} from "react";
import Card from "./Card";

function Content(props) {
    const AppBD = props.newBD;
    const setterF = props.setterFunc;

    const databaseToPrint = AppBD.map(product => {
        return (
            <Card newDatabase={product} setF={setterF}/>
        )
    });
    return (
        <div className="content">
            {databaseToPrint}
        </div>
    );
}
export default Content;

